#timer-label{
    position: absolute;
    top: 10px;
    text-align: center;
    left: 50%;
    transform: translate(-50%);
    font-size: 30px;
    font-weight: bold;
   
}
.timeViewer{
    width:  200px;
    height: 50px;
    background-color: rgb(2, 31, 58);
    position: absolute;
    top: 100px;
    left: 90px;
    border-color: rgb(221, 8, 8);
    border: solid black;

}

.timerZone{
    width: 380px;
    height: 380px;
    border: solid black;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 350px;
    border-color: #eee;
    border-radius: 100%;
    background-color: black;
    font-family: "Digital";
}
#time-left{
    position: absolute;
    top: 100px;
    font-family: "Digital";
    font-size: 40px;
    left: 50%;
    transform: translate(-50%);
    font-family: "Digital";
    color: white;
    
}

.time{

    position: absolute;
    top: 100px;
   
    font-size: 40px;
    left: 50%;
    transform: translate(-50%);
   
}

.breakArowAp{
    position: relative;
    border: solid black;
    width: 100px;
    height: 100px;
    border-radius: 100%;
    background-color: black;
    
}
#one{
    left: -44px;
    top: 46px;
}

#sesTwo{
    top: -60px;
    left: 330px;

}
#break-length{
    position: relative;
    color: white;
    top: 20px;
    font-size: 30px;
    left: -9px;
}
#session-length{
    position: relative;
    left: -4px;
    color: white;
    top: 20px;
    font-size: 30px;
}

